"use client"

import {
  createContext,
  useContext,
  useEffect,
  useRef,
  useState,
  type ReactNode,
  type ChangeEvent,
} from "react"
import { Volume2, VolumeX, Pause, Play } from "lucide-react"

type MusicContextValue = {
  volume: number
  muted: boolean
  playing: boolean
  setVolume: (v: number) => void
  toggleMute: () => void
  togglePlay: () => void
}

const MusicContext = createContext<MusicContextValue | null>(null)

// Make sure this file exists at public/audio/bg-music.mp3
const MUSIC_SRC = "/audio/bg-music.mp3"

export function MusicProvider({ children }: { children: ReactNode }) {
  const audioRef = useRef<HTMLAudioElement | null>(null)
  const [playing, setPlaying] = useState(false)
  const [volume, setVolumeState] = useState(0.35)
  const [muted, setMuted] = useState(false)

  // Initial setup + first autoplay attempt
  useEffect(() => {
    const el = audioRef.current
    if (!el) return

    el.loop = true
    el.volume = volume

    el
      .play()
      .then(() => {
        setPlaying(true)
      })
      .catch(() => {
        // Autoplay blocked; we'll try again on first interaction
        setPlaying(false)
      })
  }, [])

  // Fallback: start playing on first user interaction (any click/tap)
  useEffect(() => {
    const handleFirstInteraction = () => {
      const el = audioRef.current
      if (!el) return

      el
        .play()
        .then(() => setPlaying(true))
        .catch(() => {
          // If still blocked, no crash – user can try again
        })
    }

    window.addEventListener("pointerdown", handleFirstInteraction, {
      once: true,
    })

    return () => {
      window.removeEventListener("pointerdown", handleFirstInteraction)
    }
  }, [])

  const setVolume = (v: number) => {
    const clamped = Math.max(0, Math.min(1, v))
    setVolumeState(clamped)
    if (audioRef.current) {
      audioRef.current.volume = clamped
      if (clamped === 0) {
        setMuted(true)
        audioRef.current.muted = true
      } else {
        setMuted(false)
        audioRef.current.muted = false
      }
    }
  }

  const toggleMute = () => {
    setMuted((prev) => {
      const next = !prev
      if (audioRef.current) {
        audioRef.current.muted = next
      }
      return next
    })
  }

  const togglePlay = () => {
    const el = audioRef.current
    if (!el) return

    if (playing) {
      el.pause()
      setPlaying(false)
    } else {
      el
        .play()
        .then(() => setPlaying(true))
        .catch(() => {
          // Still blocked, but that's fine – no crash
        })
    }
  }

  return (
    <MusicContext.Provider
      value={{
        volume,
        muted,
        playing,
        setVolume,
        toggleMute,
        togglePlay,
      }}
    >
      {/* Hidden audio element that everything controls */}
      <audio ref={audioRef} src={MUSIC_SRC} />
      {children}
    </MusicContext.Provider>
  )
}

export function useMusic() {
  const ctx = useContext(MusicContext)
  if (!ctx) {
    throw new Error("useMusic must be used within a MusicProvider")
  }
  return ctx
}

/* ---------- Footer controls (unused now, but kept in case you want later) ---------- */

export function FooterMusicControls() {
  const { volume, muted, setVolume, toggleMute, playing, togglePlay } =
    useMusic()

  const handleSliderChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = Number(e.target.value)
    setVolume(value / 100)
  }

  return (
    <div className="flex items-center gap-2 rounded-full bg-emerald-900/80 px-3 py-1 shadow-sm shadow-emerald-500/30">
      {/* Play / pause */}
      <button
        type="button"
        onClick={togglePlay}
        className="flex h-7 w-7 items-center justify-center rounded-full bg-emerald-500 text-slate-950 hover:bg-emerald-400 transition"
        aria-label={playing ? "Pause music" : "Play music"}
      >
        {playing ? <Pause className="h-3 w-3" /> : <Play className="h-3 w-3" />}
      </button>

      {/* Mute */}
      <button
        type="button"
        onClick={toggleMute}
        className="text-emerald-100 hover:text-emerald-300 transition"
        aria-label={muted ? "Unmute" : "Mute"}
      >
        {muted || volume === 0 ? (
          <VolumeX className="h-4 w-4" />
        ) : (
          <Volume2 className="h-4 w-4" />
        )}
      </button>

      {/* Volume slider */}
      <input
        type="range"
        min={0}
        max={100}
        value={muted ? 0 : Math.round(volume * 100)}
        onChange={handleSliderChange}
        className="h-1 w-20 cursor-pointer accent-emerald-400"
      />
    </div>
  )
}

/* ---------- Floating in-game vertical slider ---------- */

export function FloatingGameVolume() {
  const { volume, setVolume, muted, toggleMute } = useMusic()

  const handleSliderChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = Number(e.target.value)
    setVolume(value / 100)
  }

  const sliderValue = muted ? 0 : Math.round(volume * 100)

  return (
    <div className="fixed bottom-24 right-4 z-40 flex flex-col items-center gap-2 rounded-2xl border border-emerald-500/40 bg-slate-900/95 px-3 py-3 shadow-lg shadow-emerald-500/30 backdrop-blur">
      {/* Mute / unmute icon */}
      <button
        type="button"
        onClick={toggleMute}
        className="text-emerald-200 hover:text-emerald-400 transition"
        aria-label={muted ? "Unmute" : "Mute"}
      >
        {muted || sliderValue === 0 ? (
          <VolumeX className="h-4 w-4" />
        ) : (
          <Volume2 className="h-4 w-4" />
        )}
      </button>

      {/* Vertical slider */}
      <div className="h-24 flex items-center">
        <input
          type="range"
          min={0}
          max={100}
          value={sliderValue}
          onChange={handleSliderChange}
          className="w-24 h-1 origin-center -rotate-90 cursor-pointer accent-emerald-400"
        />
      </div>
    </div>
  )
}
